







































































































































  /*撑起整个页面*/
#body{
  width:100%;
  height:100%;
  background: url("../../assets/login_images/loginBack.png") no-repeat center center;
  background-size: 100% 100%;
  overflow: hidden;
}
  /*中部内容的样式*/
#content{
  width: 60%;
  min-width: 700px;
  height: 100%;
  background:url("../../assets/login_images/fromBack.png") top right no-repeat;
  background-size: 100% 100%;
  float: right;
  img{
    width:70%;
    display: inline-block;
  }
  /* 登录输入框的样式 */
  .login-box{
    &>p {
      width: 60%;
      margin: 10% auto 10%;
      transform: translateX(15%);
      img {
        width: 100%;
      }
    }
    .login-box-left{
      width: 420px;
      padding: 30px 45px;
      text-align: center;
      background: #fff;
      border-radius: 8px;
      margin: auto;
      transform: translateX(20%);
      /*修改input验证报错高度*/
      .ivu-form-item{
        margin-bottom: 20px;
        .ivu-form-item-error-tip{
          font-size: 10px;
          padding-top: 2px;
        }
      }
      p{
        margin:0;
        text-align: left;
        line-height: 22px;
      }
      .fromItems{
        margin-bottom:20px;
        position: relative;
        i {
          width: 16px;
          height: 16px;
          display: block;
          position: absolute;
          z-index: 1000;
          right: 10px;
          top: 5px;
          img {
            width: 100%;
            height: 100%;
          }
          &.locked {
            width: 18px;
            height: 18px;
            top: 7px;
          }
        }
      }
      .fromItems >>> input {
        height: 38px !important;
      }
      .verificationCode_box{
        margin-bottom: 10px;
        position: relative;
        .verificationCode{
          position: absolute;
          width:75px;
          height:28px;
          background: #ddd;
          right: 10px;
          top: 4px;
        }
      }
      .fromBtns {
        margin-top: 30px !important;
        margin-bottom: 0;
        overflow: hidden;
        .ivu-checkbox-wrapper {
          width: 50%;
          float: left;
          text-align: left;
          margin: 0;
        }
        .ivu-btn {
          width: 50%;
          float: right;
        }
      }
    }
  }
}
/*底部内容的样式*/
#footer{
  position: fixed;
  width: 100%;
  bottom: 0;
  left: 0;
  height: 42px;
  line-height: 18px;
  p{
    text-align: center;
    font-size: 12px;
    color: #fff;
    letter-spacing: 1px;
    span {
      display: block;
    }
  }
}
